                price: 99.9,
              },
            ]
          }
        ]
      };
    },
    mounted() {
      this.initializeRoll(); //滑动初始化方法
      this.initializeOwn(); //初始化rightSide方法
    },
    //计算属性
    computed: {
      // 计算出当前分类的下标
      currentIndex() {
        const {
          scrollY,
          rightSide
        } = this; //拿到每次滑动的值以及每一个分类的下标值
        console.log('滚动的值及每一个分类的下标值...', this)
        //findIndex() 方法返回传入一个测试条件（函数）符合条件的数组第一个元素位置
        //获取数组rightSide中每一个下标值并筛选条件返回第一个元素索引位置
        const index = rightSide.findIndex((top, index) => {
          // scrollY>=当前top && scrollY<下一个top
          return scrollY >= top && scrollY < rightSide[index + 1];
        });
        // 返回结果
        return index;
      }
    },
    methods: {
      // 初始化滚动
      initializeRoll() {
        // new一个类的实例，前提必须确保父元素和子元素的内容已经正确渲染了，不然不会生效
        new BScroll(".leftNav", {
          click: true
        });
        // 组件实例上挂属性
        this.foodsScroll = new BScroll(".rightContant", {
          probeType: 2, // 因为惯性滑动不会触发
          click: true
        });

        // 给右侧列表绑定scroll监听
        this.foodsScroll.on("scroll", ({
          x,
          y
        }) => {
          this.scrollY = Math.abs(y);
        });
        // 给右侧列表绑定scroll结束的监听
        this.foodsScroll.on("scrollEnd", ({
          x,
          y
        }) => {
          console.log("操作结束的下标值" + x, y);
          this.scrollY = Math.abs(y);
        });
      },
      // 初始化rightSide
      initializeOwn() {
        // 初始化rightSide
        const rightSide = [];
        let top = 0;
        rightSide.push(top);
        // 找到所有分类下的li
        const lis = this.$refs.shopContant.getElementsByClassName("eventList"); //获取dom元素lii（eventList），对其进行操作
        // slice() 方法返回一个新的数组对象
        Array.prototype.slice.call(lis).forEach(li => {
          top += li.clientHeight; //相当于top = top + li.clientHeight
          rightSide.push(top);
        });

        //更新数据
        this.rightSide = rightSide;
        console.log("每一个分类的下标" + rightSide);
      },
      //左侧点击事件
      onClickNav(index) {
        console.log(index);
        //拿到目标位置的scrollY，点击左侧导航，右侧列表滑动到对应的位置
        const scrollY = this.rightSide[index];
        // 点击的分类成为当前分类，索引当前选中的导航标题
        this.scrollY = scrollY;
        // 平滑滑动右侧列表
        this.foodsScroll.scrollTo(0, -scrollY, 1000);
      }
    }
  };
</script>
<style scoped>
  .commodity {
    /* 最大的盒子样式 */
    display: flex;
    position: fixed;
    top: 0px;
    bottom: 0px;
    width: 100%;
    background: #fff;
    overflow: hidden;
  }

  .leftNav {
    /* 左侧大盒子的整体样式 */
    width: 80px;
    background: rgb(248, 248, 248);
  }

  .typeStyle {
    /* 左侧导航选中的样式 */
    background: #fff;
    font-weight: bold;
    display: flex;
    align-items: center;
    color: rgb(255, 94, 121);
    justify-content: center;
    border-left: 2px solid rgb(255, 94, 121);
  }

  .leftTit {
    /* 左侧导航标题默认样式 */
    display: flex;
    height: 40px;
    justify-content: center;
    font-size: 12px;
    align-items: center;
  }

  .rightContant {
    /* 右侧大盒子的整体样式 */
    flex: 1;
  }

  .rightTtitle {
    /* 右侧标题样式 */
    padding-left: 12px;
    height: 26px;
    line-height: 26px;
    font-size: 12px;
    font-weight: bold;
    color: rgb(147, 153, 159);
    background: rgb(248, 248, 248);
    margin: 0;
  }

  .gather {
    /* 右侧每一个商品的样式 */
    display: flex;
    margin: 16px;
    display: flex;
    align-items: center;
  }

  .distance {
    /* 右侧图片与描述间距 */
    padding-left: 6px;
  }

  .gather img {
    /* 右侧图片样式 */
    width: 60px;
    height: 60px;
    vertical-align: middle;
  }

  :last-child {
    /* 点击左侧导航右侧内容与左边对齐 */
    margin-bottom: 0;
  }

  .shopName {
    /* 右侧商品标题字体大小 */
    font-size: 14px;
  }

  .saleGood {
    /* 右侧月售好评样式 */
    font-size: 10px;
    color: rgb(147, 153, 159);
    padding: 4px 0px;
  }

  .saleGood span:first-child {
    /* 月售样式 */
    padding-right: 10px;
  }

  .price {
    /* 价格样式 */
    font-weight: bold;
    font-size: 14px;
    color: rgb(255, 94, 121);
  }
</style>



